





























import uniIcon from '@/components/uni-icon.vue';
let userInfo;
export default {
	components: {
		uniIcon
	},
	data() {
		return {
			//headerPosition:"fixed",
			categoryList: [],
			subCategoryList: [],
			height: 0,
			categoryActive: 0,
			scrollTop: 0,
			scrollHeight: 0,
			name: "七月_"
		}
	},
	onLoad(e) {
		userInfo = uni.getStorageSync('userLoginInfo');
		if (userInfo.identity) {
			this.identity = userInfo.identity;
		}
	},
	methods: {
		scroll(e) {
			this.scrollHeight = e.detail.scrollHeight;
		},
		categoryClickMain(categroy, index) {
			this.categoryActive = index;
			this.subCategoryList = categroy.subCategoryList;
			this.scrollTop = -this.scrollHeight * index;
		},
		getCategory() {
			uni.showLoading({
				title: "数据加载中..."
			});
			uni.request({
				url: this.apiUrl + 'goods/getCategory',
				method: 'GET',
				data: {
					identity:this.identity,
				},
				success: res => {
					if (res.data.code == 0) {

						for (var i = 0; i < res.data.data.length; i++) {
							var subList = [];
							// 对应分类商品
							var goodsLis = res.data.data[i].child;

							for (var j = 0; j < goodsLis.length; j++) {
								subList.push({
									"NAME": goodsLis[j].cat_name,
									"IMG": goodsLis[j].image,
									"cid": goodsLis[j].cid
								})
							}
							//console.log(subList);
							// 分类
							this.categoryList.push({
								"NAME": res.data.data[i].cat_name,
								"subCategoryList": subList
							})

						}
						this.subCategoryList = this.categoryList[0].subCategoryList;
					}
				},
				fail: () => {
					uni.showToast({
						title: "获取数据失败",
						icon: "none"
					})
				},
				complete: () => {
					uni.hideLoading();
				}
			});
		},
	},
	onLoad: function() {
		this.getCategory();
		this.height = uni.getSystemInfoSync().windowHeight;
	}
}
